import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import axios from 'axios';
import store from 'store';
import { backendUrl } from '../../config/config';
import { useTranslation } from 'react-i18next';
import { useCustomStore } from '../../context/useStore';
import { blockedFilters } from '../../config/config';
import { Helmet } from 'react-helmet';
import LazyLoad from 'react-lazy-load';

import './index.scss';
import CustomFoodBlock from '../../components/CustomFoodBlock';
import HeadingBlock from '../../components/HeadingBlock';
import MobileFilters from '../../components/MobileFilters/MobileFilters';
import Spinner from '../../components/Spinner';
import ErrorIndicator from '../../components/Errors/ErrorIndicator';

import { ACTIONS } from '../../context/providerContext';

const PageWrapper = React.memo((props) => {
  const { t } = useTranslation();
  const location = useLocation();
  const [filteredGoods, setFilteredGoods] = useState([]);
  const [article, setArticle] = useState(false);
  const [response, setResponse] = useState([]);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState(false);

  const [rollsFilters, setRollsFilters] = useState(null);
  const [selectedRollFilter, setSelectedRollFilter] = useState(null);

  const { state, dispatch } = useCustomStore();

  useEffect(() => {
    setLoading(true);

    if (location.pathname.includes('rolls')) {
      axios
        .get(backendUrl + '/rollFilters/rollFilter')
        .then((response) => {
          console.log(response);
          setRollsFilters(response.data.fils);
        })
        .catch((err) => {
          console.log(err);
        });
    }

    axios
      .get(backendUrl + props.url)
      .then((res) => {
        console.log(res);
        if (props.type === 'filter') {
          setResponse(res.data);
        } else {
          setResponse(res.data.products);
        }
        setArticle(res.data.article);
        props.setBreadProd(res.data.products);
        setErr(false);
      })
      .catch((err) => {
        setErr(true);
      })
      .finally(() => setLoading(false));
  }, []);

  const onLoadIFrame = (obj) => {
    //  obj.target.style.height ='1800px';
    obj.target.style.height = 300 + obj.target.contentWindow.document.documentElement.scrollHeight + 'px';

    // obj.target.style.width = obj.target.contentWindow.document.documentElement.width + 'px';
  };

  const createJSONLD = () => {
    const data = {
      '@context': 'https://schema.org/',
      '@type': 'ItemList',
      description: `${props.title} Доставка. Заказывай ${props.title} с бесплатной доставкой в Днепре, Запорожье и Харькове✔️${props.title} от 15грн ☎ 050 719-24-24 ✔️ Онлайн заказ еды на дом,офис.`,
      name: props.title,
      numberOfItems: filteredGoods.length,
      itemListElement: filteredGoods.map((el) => ({
        '@type': 'Product',
        category: props.title,
        material: props.title,
        productID: el._id,
        slogan: el.name_ru,
        name: el.name_ru,
        brand: {
          '@type': 'Organization',
          name: '24ROLLS',
          address: 'Днепр, Запорожье',
          location: 'Днепр, Запорожье',
        },
        description: el.description_ru,
        image: el.image,
        offers: {
          '@type': 'Offer',
          offeredBy: {
            '@type': 'Organization',
            name: '24ROLLS',
            address: 'Днепр, Запорожье',
            location: 'Днепр, Запорожье',
          },
          seller: {
            '@type': 'Organization',
            name: '24ROLLS',
            address: 'Днепр, Запорожье',
            location: 'Днепр, Запорожье',
          },
          category: props.title,
          priceCurrency: 'UAH',
          price: el.percent !== 0 ? el.price * ((100 - el.percent) / 100) : el.price,
        },
      })),
    };

    return JSON.stringify(data);
  };

  useEffect(() => {
    const cityShop = store.get('24rolls').customOptions.city;

    if (response) {
      const goodsWithCity = response.filter((el) => {
        let valid = false;
        if (cityShop === 'Dnipro') {
          el.shop.map((el) => {
            if (el.name === 'Dp') valid = true;
          });
        } else if (cityShop === 'Zaporijja') {
          el.shop.forEach((el) => {
            if (el.name === 'Zp') valid = true;
          });
        } else if (cityShop === 'Kharkov') {
          el.shop.forEach((el) => {
            if (el.name === 'Kh') valid = true;
          });
        }

        return valid;
      });
      setFilteredGoods(goodsWithCity);
      props.setBreadProd(goodsWithCity);
    }

    if (state.filterMode) {
      setLoading(true);
      axios
        .get(backendUrl + `/products/filter/category/${props.categoryId}/${state.filterMode}`)
        .then((res) => {
          console.log(res);
          const newFilteredGoods = res.data.products.sort(() => Math.random() - 0.5);
          setFilteredGoods(newFilteredGoods);
          setArticle(res.data.article);
          props.setBreadProd(res.data.products);
          setErr(false);
        })
        .catch((err) => {})
        .finally(() => setLoading(false));
    }
  }, [state.filterMode, response]);

  useEffect(() => {
    if (selectedRollFilter) {
      setLoading(true);
      axios
        .post(backendUrl + '/rollFilters/products/rollFilter/', {
          rollFilters: [selectedRollFilter],
        })
        .then((response) => {
          console.log(response);
          setFilteredGoods(response.data);
        })
        .catch((err) => {
          console.log(err, err.response);
          setErr(true);
        })
        .finally(() => setLoading(false));
    }
  }, [selectedRollFilter]);

  const { city, type, filters } = props;

  return (
    <>
      <div className={'rolls_container'}>
        <Helmet>
          <script async type='application/ld+json'>
            {createJSONLD()}
          </script>
          <title>
            {city === 3 || type === 'filter'
              ? props.el.seo_title
              : city === 2
              ? props.el.seo_title_zp
              : props.el.seo_title_dp}
          </title>
          <meta
            name='description'
            content={
              city === 3 || type === 'filter'
                ? props.el.seo_description
                : city === 2
                ? props.el.seo_description_zp
                : props.el.seo_description_dp
            }
          />
          <meta
            name='keywords'
            content={
              city === 3 || type === 'filter'
                ? props.el.seo_keywords
                : city === 2
                ? props.el.seo_keywords_zp
                : props.el.seo_keywords_dp
            }
          />
          <script async type='application/ld+json'>
            {createJSONLD()}
          </script>
          <script type='application/ld+json'>
            {JSON.stringify({
              '@context': 'http://schema.org',
              '@type': 'WebSite',
              '@id': '#website',
              url: 'https://24rolls.com.ua/',
              name: '\u0421\u0443\u0448\u0438 24Rolls Dnepr',
              potentialAction: {
                '@type': 'SearchAction',
                target: 'https://24rolls.com.ua/?s={search_term_string}',
                'query-input': 'required name=search_term_string',
              },
            })}
          </script>
          <script type='application/ld+json'>
            {JSON.stringify({
              '@context': 'http://schema.org',
              '@type': 'Organization',
              url: 'https://24rolls.com.ua/',
              contactPoint: [
                {
                  '@type': 'ContactPoint',
                  telephone: '068 719 24 24',
                  contactType: ['customer service', 'sales'],
                  areaServed: ['UA'],
                  contactOption: ['TollFree'],
                  availableLanguage: ['Russian', 'Ukrainian'],
                },
              ],
              sameAs: [
                'https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA',
                'https://www.instagram.com/24rolls.com.ua/',
              ],
            })}
          </script>
        </Helmet>
        {loading && <Spinner />}
        {err && !loading && <ErrorIndicator />}
        {!err && !loading && (
          <>
            <HeadingBlock title={t(props.title)} svg={true} h={'h1'} />

            {rollsFilters && (
              <div className='page-wrapper_filters page-wrapper_rolls-filters'>
                {rollsFilters.map((el) => (
                  <div
                    className={selectedRollFilter === el._id ? 'item active' : 'item'}
                    onClick={() => {
                      setSelectedRollFilter(el._id);
                      dispatch({ type: ACTIONS.DROP_FILTER });
                    }}
                  >
                    <div>
                      <LazyLoad debounce={false} offsetVertical={500}>
                        <img alt={el.alt} src={backendUrl + '/' + el.image} />
                      </LazyLoad>
                    </div>
                    <p>{el.name_ru}</p>
                  </div>
                ))}
              </div>
            )}

            <MobileFilters filters={filters} setSelectedRollFilter={setSelectedRollFilter} />

            {filters && (
              <div className='page-wrapper_filters'>
                {filters.map((el) => {
                  if (blockedFilters.includes(el._id)) {
                    return null;
                  } else {
                    return (
                      <div
                        className={state.filterMode === el._id ? 'item active' : 'item'}
                        onClick={() => {
                          setSelectedRollFilter(null);
                          state.filterMode === el._id
                            ? dispatch({ type: ACTIONS.DROP_FILTER })
                            : dispatch({
                                type: ACTIONS.SET_FILTER,
                                filterId: el._id,
                              });
                        }}
                      >
                        <div>
                          <LazyLoad debounce={false} offsetVertical={500}>
                            <img alt={el.alt} src={backendUrl + '/' + el.image} />
                          </LazyLoad>
                        </div>
                        <p>{el.name_ru}</p>
                      </div>
                    );
                  }
                })}
              </div>
            )}

            {filteredGoods && (
              <div className={'rolls_container_block'}>
                {filteredGoods.map((el) => (
                  <CustomFoodBlock
                    {...props}
                    style={'open-modal'}
                    key={el._id}
                    food={el}
                    width={window.innerWidth < 610 ? true : false}
                    smallMode={window.innerWidth < 450 ? true : false}
                  />
                ))}
              </div>
            )}
            {!filteredGoods[0] && !loading && !err && <p> {'Нет данных товаров...'} </p>}
          </>
        )}
      </div>
      {article && !err && !loading && (
        <div style={{ height: '100%' }}>
          <iframe
            onLoad={onLoadIFrame}
            style={{
              width: '100%',
              border: 'none',
              height: '100%',
              verflow: 'visible',
              fontFamily: "'Roboto', sans-serif",
            }}
            srcDoc={article}
          >
            {'Ваш браузер не поддерживает Frame'}
          </iframe>
        </div>
      )}
    </>
  );
});

export default PageWrapper;
